@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
:root {
  --width-searchbar: clamp(205px, 33vw, 300px);
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.filters {
  width: auto;
  height: 100%;
  display: flex;
  align-items: center;
}

.filters > label {
  color: #fff;
}
.searchbar {
  width: clamp(205px, 33vw, 300px);
  width: var(--width-searchbar);
  height: 30px;
  padding-left: 5px;
  margin: 0 5px 0 5px;
  outline: none;
  border: none;
  border-radius: 10px;
  box-sizing: border-box;
  font-size: 16px;
  transition: box-shadow 0.6s linear;
}

.searchbar:focus {
  box-shadow: 0px 0px 0px 5px #878b8b;
}

.searchbar::-webkit-input-placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.searchbar:-ms-input-placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.searchbar::placeholder {
  padding-left: 10px;
  color: #878b8b;
}

.searchmenu {
  width: 100px;
  padding: 0 5px;
  position: absolute;
  top: 40px;
  left: 60px;
  background-color: #fff;
  line-height: 40px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  display: none;
  z-index: 1;
}

.filters:hover .searchmenu,
.searchbar:focus + .searchmenu {
  animation: slide-down 0.5s ease-out;
  display: block;
}

.clear {
  background-color: #0e0e2c;
  font-size: 12px;
  font-weight: 600;
  color: #fff;
  text-transform: uppercase;
  padding: 10px;
  margin: 1px;
  border: none;
  border-radius: 10px;
}

.clear:hover {
  box-shadow: 0px 0px 0px 3px #878b8b;
}

@keyframes slide-down {
  0% {
    opacity: 0;
    transform: translateY(-20%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

#header-component {
  height: 48px;
  background-color: #363c3c;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

#header-component > a {
  font-size: 2em;
  font-weight: bolder;
  color: #fff;
  text-decoration: none;
  margin: 0 auto 0 25px;
}

@media only screen and (max-width: 675px) {
  #header-component {
    height: auto;
    flex-direction: column;
  }
}

.range-wrap {
  color: #fff;
  display: flex;
  position: relative;
  margin: 0 15px 0 0;
}
.range {
  width: 100%;
}
.bubble {
  pointer-events: none;
  opacity: 0;
  background: #000;
  color: #fff;
  padding: 2px 4px;
  position: absolute;
  border-radius: 4px;
  left: 50%;
  transform: translateX(-50%);
}
.bubble::after {
  content: "";
  position: absolute;
  width: auto;
  height: 2px;
  background: #000;
  top: -1px;
  left: 50%;
}

.show {
  opacity: 1;
}

.dropdown {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 20px;
  border: none;
  outline: none;
  color: #fff;
  padding: 12px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.dropdown:hover .dropbtn {
  color: #878b8b;
}

.dropdown-content {
  z-index: 1;
  border-radius: 5px;
  display: none;
  position: absolute;
  background-color: #fff;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.dropdown:hover .dropdown-content {
  animation: slide-down 0.5s ease-out;
  display: block;
}

.grid-column {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

@media only screen and (max-width: 675px) {
  .dropdown-content {
    min-width: 100%;
    position: fixed;
    left: 0;
  }
}

